.marginAuto{
  margin: 0 auto;
}
.body{
  width: 100%;
  min-height: 100vh;
  padding: 10px;
}
.headerPos{
  justify-content: space-between;
}

.App{
  min-height: 100vh;
  position: relative;
}

.header{
  width: 100%;
  height: 50px;
  background-color: crimson;
  text-align: center;


  padding: 3px;
}

.HeaderText{
    color: white;
}

.width-15{
  width: 13%;
  align-items: center;
}

.navPos{
  width: 15%;
  display: flex;
  justify-content: space-around;
}


.width-90{
    width:90%;
}
.width-80{
    width:80%;
}
.movies{
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 10px;




}
.video{
    margin: 0 auto;
    width: 560px;
}
.wrapper{
    width: 100%;
    height: 281px;
    overflow: hidden;
}

.movieTable{
    margin-top: 25px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    padding: 5px;
    height:550px;
    width: 500px;
    position:relative;
}
.textOverview{
  overflow-wrap: inherit;
  width: 100%;
  height: 167px;
  text-overflow: ellipsis;
  overflow: hidden;

}

.float{
    float: right;
}
.buttonMorePosition{
    position: absolute;
    left:86%;
    top:92%;
}

.buttonAddPosition{
  position: absolute;
  left:76%;
  top:92%;
}

.buttons{
    text-decoration: none;
    color: white;
    background-color: crimson;
    width: 75px;
    height: 32px;
    border-style: none;
    margin-top: 10px;
    border-radius: 3px;
    cursor: pointer;
    transition: all .3s;
}
.poster{
    height: 55vh;
}
.d-flex{
    display: flex;
}
.container{
    margin-bottom: 20px;
    margin-top: 15px;
    width:90%;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    padding: 5px;
}
.imgDiv{
    margin-right: 10px;
}

Link{
    text-decoration: none;
    color: white;
}

h2{
    word-wrap:break-word;
}

.recommendImg{
    width: 100%;
    height: 250px;
}
.movieTableRecommends{
    margin-top: 15px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    padding: 5px;
    height:310px;
    width: 200px;
    position:relative;
}





.margin{
    margin-bottom: 15px;
}

.red{
  background: crimson !important;
}

.btnMargin{
  margin: 0 10px;
}

.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0, 0.5);
}
.popup_inner {
  position: absolute;
  left: 10%;
  height: 100vh;
  margin: auto;
  background: white;
  padding-left: 20px;
}


html, body, .App {
  margin: 0 auto;
  padding: 0;
  position: relative;
  width: 100%;

}

.popImg{
  width: 100%;
  height: 230px;
}

.f-wrap{
  display: flex;
  flex-wrap: wrap;
}

.movieTablePop{
  margin-top: 15px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
  padding: 5px;
  height:310px;
  width: 200px;
  position:relative;
  margin-right: 12px;
}

.popButton{
  width: 50px;
  height: 50px;
  position: absolute;
  left: -5%;

}

.input{
  width: 30% !important;
}

.search{
  width: 290px;
  margin-top: 15px;
  align-items: center;

}



/*darkmode*/

.darkBodySite{
  background-color: #121212 !important;
  color: seashell;

}

.darkBody{
  background-color: #292929;
}

.darkHeader{
  background-color: #1D1D1D !important;

}

.darkTableMovie{
  color: seashell !important;
  background-color: #1D1D1D;
}

.darkPopUp{
  background-color:#121212 !important;
  color: seashell !important;
}

.colorText{
  text-decoration: none;
  color: seashell;
}

.popScroll{
  overflow: auto;
}